"use client";
import { Input } from "@/components/ui/input";
import {
  IconChartBar,
  IconKey,
  IconLogout,
  IconPlus,
  IconSettings,
  IconTimeline,
  IconUser,
} from "@tabler/icons-react";
import { useRouter } from "next/navigation";

export default function RolePage() {
  const router = useRouter();

  return (
    <div id="container-user" className="flex min-h-screen">
      <div
        id="navigation"
        className="bg-white w-[250px] flex flex-col items-center gap-8 p-5"
      >
        <h1 className="text-3xl text-black font-bold">Connect</h1>
        <div className="flex flex-col gap-3">
          <button
            onClick={() => router.push("/users")}
            className="bg-black text-white w-[150px] p-1 rounded-lg flex gap-2"
          >
            <IconUser />
            User
          </button>
          <button
            onClick={() => router.push("/roles")}
            className="text-black bg-white hover:bg-black hover:text-white w-[150px] p-1 rounded-lg flex gap-2"
          >
            <IconKey />
            Hak Akses
          </button>
          <button
            onClick={() => router.push("/log")}
            className="text-black bg-white hover:bg-black hover:text-white w-[150px] p-1 rounded-lg flex gap-2"
          >
            <IconTimeline />
            Log
          </button>
          <button
            onClick={() => router.push("/report")}
            className="text-black bg-white hover:bg-black hover:text-white w-[150px] p-1 rounded-lg flex gap-2"
          >
            <IconChartBar />
            Laporan
          </button>
          <button
            onClick={() => router.push("/settings")}
            className="text-black bg-white hover:bg-black hover:text-white w-[150px] p-1 rounded-lg flex gap-2"
          >
            <IconSettings />
            Pengaturan
          </button>
          <button
            onClick={() => router.push("/logout")}
            className="text-black bg-white hover:bg-black hover:text-white w-[150px] p-1 rounded-lg flex gap-2"
          >
            <IconLogout />
            Logout
          </button>
        </div>
      </div>
      <div id="content" className="bg-white w-full p-5">
        <Input placeholder="Cari User" />
        <div id="list-user" className="mt-5 flex-col gap-2">
          <div
            id="user-card"
            className="border p-3 rounded-lg flex justify-between items-center"
          >
            <div>
              <div>
                <p className="text-lg font-bold">Refinda CW</p>
                <p className="text-sm">refindacw@universitasmulia.ac.id</p>
              </div>
              <div className="flex gap-2 mt-3">
                <p className="bg-black text-white p-1 text-sm rounded-lg">
                  Admin
                </p>
                <p className="bg-black text-white p-1 text-sm rounded-lg">
                  Employee
                </p>
              </div>
            </div>
            <p>Aktif</p>
          </div>
          <div
            id="user-card"
            className="border p-3 rounded-lg flex justify-between items-center"
          >
            <div>
              <div>
                <p className="text-lg font-bold">Sakuma Rei</p>
                <p className="text-sm">undead4life@universitasmulia.ac.id</p>
              </div>
              <div className="flex gap-2 mt-3">
                <p className="bg-black text-white p-1 text-sm rounded-lg">
                  Employee
                </p>
              </div>
            </div>
            <p>Aktif</p>
          </div>
          <div
            id="user-card"
            className="border p-3 rounded-lg flex justify-between items-center"
          >
            <div>
              <div>
                <p className="text-lg font-bold">Kashuu Kiyomitsu</p>
                <p className="text-sm">mawarmerah@universitasmulia.ac.id</p>
              </div>
              <div className="flex gap-2 mt-3">
                <p className="bg-black text-white p-1 text-sm rounded-lg">
                  Employee
                </p>
              </div>
            </div>
            <p>Aktif</p>
          </div>
          <div
            id="user-card"
            className="border p-3 rounded-lg flex justify-between items-center"
          >
            <div>
              <div>
                <p className="text-lg font-bold">Ennoshita Chikara</p>
                <p className="text-sm">
                  karasuno2kapten@universitasmulia.ac.id
                </p>
              </div>
              <div className="flex gap-2 mt-3">
                <p className="bg-black text-white p-1 text-sm rounded-lg">
                  Employee
                </p>
              </div>
            </div>
            <p>Suspended</p>
          </div>
          <button className="fixed bottom-6 right-6 p-3 full bg-gray-300 full hover:bg-black hover:text-white">
            <IconPlus size={25} />
          </button>
        </div>
      </div>
    </div>
  );
}
